.profile {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    flex-wrap: wrap;
    width: 300px;
    height: 400px;
    border: solid 1px grey;
    border-radius: 2px;
    padding: 50px 0px;
    margin: auto;
}
.description {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.avatar {
    width: 100px;
    height: 100px;
    border-radius: 100%;
}
.name {
    font-size: large;
    font-weight: bold;
}
.tag {
    color: grey;
}
.location {
    color: grey;
}
